import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';

function Payment() {
	const location = useLocation();
	const bookData = location.state.bookData;

	// 
	const [opened, setOpened] = useState([]);
	const openAccord = (i) => {
		if (opened.includes(i)) {
			setOpened((prev) => [...prev].filter((el) => el !== i));
		} else {
			setOpened((prev) => [...prev, i]);
		}
	};
	const openAccordAll = () => {
		setOpened(
			Array(terms.length)
				.fill()
				.map((v, i) => i),
		);
	};
	const closeAccordAll = () => {
		setOpened([]);
	};

	// 
	const terms = [
		{
			title: '()   ',
			content: `        .<br><br>      ,      .<br><br>           .<br><br>           .<br><br>           .<br><br>  2         .<br><br>       .`,
		},
		{
			title: '() (   ) ',
			content: `   "/"  .<br><br>    .<br><br>       .<br><br>   (+)  .<br><br> 100%     .<br><br>   .`,
		},
		{
			title: '()     ',
			content: `              .`,
		},
		{
			title: '()  3 ',
			content: `              .`,
		},
		{
			title: '()     ',
			content: `              .`,
		},
	];
	const [allChecked, setAllChecked] = useState(false);
	const [checkedList, setCheckedList] = useState([]);
	let checkEach = (i) => {
		if (checkedList.includes(i)) {
			setCheckedList((prev) => [...prev].filter((el) => el !== i));
		} else {
			setCheckedList((prev) => [...prev, i]);
		}
	};
	const checkAll = () => {
		if (checkedList.length < 5) {
			setCheckedList(
				Array(5)
					.fill()
					.map((v, i) => i),
			);
			setAllChecked(true);
		} else {
			setCheckedList([]);
			setAllChecked(false);
		}
	};
	useEffect(() => {
		if (checkedList.length < 5) {
			setAllChecked(false);
		} else {
			setAllChecked(true);
		}
	}, [checkedList]);

	return (
		<div className="content payment">
			<div className="selected">
				<h2 className="title"></h2>
				<table>
					<colgroup>
						<col width="30%" />
					</colgroup>
					<tbody>
						<tr>
							<th></th>
							<td>{bookData.rooms}</td>
						</tr>
						<tr>
							<th></th>
							<td>{bookData.range}</td>
						</tr>
						<tr>
							<th></th>
							<td> / </td>
						</tr>
						<tr>
							<th></th>
							<td className="price">{bookData.price}</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div className="inp-info">
				<h2 className="title"></h2>
				<table>
					<colgroup>
						<col width="120px" />
					</colgroup>
					<tbody>
						<tr>
							<th>
								<label htmlFor="inp01"></label>
							</th>
							<td>
								<div className="inp-cell name">
									<input type="text" id="inp01" />
									<span className="place-holder"></span>
								</div>
								<p className="chk">    2~10  .</p>
							</td>
						</tr>
						<tr>
							<th>
								<label htmlFor="inp02"></label>
							</th>
							<td>
								<div className="inp-cell phone">
									<select>
										<option>010</option>
										<option>011</option>
										<option>017</option>
										<option>018</option>
										<option>019</option>
										<option>0502</option>
										<option>0503</option>
										<option>0504</option>
										<option>0507</option>
									</select>
									<input type="number" id="inp02" />
									<input type="number" id="inp02" />
								</div>
								<p className="chk">  .</p>
							</td>
						</tr>
						<tr>
							<th>
								<label htmlFor="inp03"></label>
							</th>
							<td>
								<div className="inp-cell email">
									<input type="email" id="inp03" />
									<span className="place-holder"></span>
								</div>
								<p className="chk"> .</p>
							</td>
						</tr>
						<tr>
							<th>
								<label htmlFor="inp04"></label>
							</th>
							<td>
								<div className="inp-cell request">
									<textarea id="inp04"></textarea>
									<span className="place-holder"></span>
								</div>
								<p className="chk">    .</p>
							</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div className="terms">
				<div className="title">
					<h2></h2>
					<div className="btns">
						<button onClick={openAccordAll}></button>
						<button onClick={closeAccordAll}></button>
					</div>
				</div>
				<button className={`check-all ${allChecked ? 'active' : ''}`} onClick={checkAll}>
					
				</button>
				<ul className="accord-wrap">
					{terms.map((q, i) => (
						<li key={i} className={opened.includes(i) ? 'accord-panel active' : 'accord-panel'}>
							<div className="accord-title">
								<div className="checkbox">
									<input type="checkbox" id={'check0' + i} onChange={() => checkEach(i)} checked={checkedList.includes(i) ? true : false} />
									<label className="h4" htmlFor={'check0' + i}>
										{q.title}
									</label>
								</div>
								<button onClick={() => openAccord(i)}>
									<span className="hide"> </span>
								</button>
							</div>
							<div className="accord-content">
								<div dangerouslySetInnerHTML={{ __html: q.content }}></div>
							</div>
						</li>
					))}
				</ul>
			</div>
			<div className="pay">
				<p> </p>
				<span>{bookData.price}</span>
				<p></p>
				<span></span>
				<span></span>
			</div>
		</div>
	);
}
export default Payment;
