import useModals from '../components/hooks/useModals';
import { modals } from '../components/modal/Modals';

import { useState, Fragment } from 'react';
import { Link } from 'react-router-dom';

import { roomData } from '../assets/js/Data';

import { DateRange } from 'react-date-range';
import { format, addDays } from 'date-fns';
import { ko } from 'react-date-range/dist/locale';

function Reservation() {
	const { openModal } = useModals();
	const roomOpen = (e) => {
		openModal(modals.Room, {
			dataId: Number(e.currentTarget.dataset.id),
		});
	};

	const ddnayo = true;

	const [selectedDate, setSelectedDate] = useState([
		{
			startDate: addDays(new Date(), 0),
			endDate: addDays(new Date(), 1),
			key: 'selection',
		},
	]);
	const dateRangeOpt = {
		ranges: selectedDate,
		months: 2,
		direction: 'horizontal',
		locale: ko,
		color: null,
		rangeColors: [null],
		minDate: addDays(new Date(), 0),
		maxDate: addDays(new Date(), 90),
		onChange: (item) => setSelectedDate([item.selection]),
		dateDisplayFormat: 'yy M d',
		monthDisplayFormat: 'yy M',
		// startDatePlaceholder: ' ',
		// endDatePlaceholder: ' ',
		// fixedHeight: true,
		showMonthAndYearPickers: false,
		editableDateInputs: false,
	};

	const [checkedList, setCheckedList] = useState([]);
	const [sumPrice, setSumPrice] = useState(0);
	const onCheckedItem = (checked, item, value) => {
		if (checked) {
			setCheckedList((prev) => [...prev, item]);
			setSumPrice((prev) => prev + parseInt(value));
		} else if (!checked) {
			setCheckedList(checkedList.filter((el) => el !== item));
			setSumPrice((prev) => prev - parseInt(value));
		}
	};

	//  
	const returnValue = {
		range: `${format(selectedDate[0].startDate, 'yy M d')}~${format(selectedDate[0].endDate, 'yy M d')}`,
		night: Math.ceil(Math.abs(selectedDate[0].endDate.getTime() - selectedDate[0].startDate.getTime()) / (1000 * 60 * 60 * 24)),
		rooms: checkedList.join(', '),
		price: sumPrice,
	};

	return (
		<div className="content reservation">
			<div className="info">
				<h2 className="title"></h2>
				<table>
					<colgroup>
						<col width="30%" />
					</colgroup>
					<tbody>
						<tr>
							<th></th>
							<td> 15:00</td>
						</tr>
						<tr>
							<th></th>
							<td> 11:00</td>
						</tr>
						<tr>
							<th> </th>
							<td>
								<ul className="list-style">
									<li className="bul">,  20,000</li>
									<li className="bul"> 10,000</li>
									<li className="bul"> 5kg 10,000, 5~20kg 20,000, 20kg  30,000</li>
								</ul>
							</td>
						</tr>
						<tr>
							<th> </th>
							<td>
								<ul className="list-style">
									<li className="bul">
										 20,000
										<ul>
											<li className="star">,   5,000</li>
										</ul>
									</li>
								</ul>
							</td>
						</tr>
						<tr>
							<th> </th>
							<td>20,000</td>
						</tr>
					</tbody>
				</table>
			</div>

			<div className="caution">
				<h2 className="title">/  </h2>
				<ul className="list-style">
					<li className="bul">        .</li>
					<li className="bul">     ,     .</li>
					<li className="bul">           .</li>
					<li className="bul">           .</li>
					<li className="bul">           .</li>
					<li className="bul">      .</li>
				</ul>
			</div>

			<div className="fee">
				<h2 class="title"> </h2>
				<div className="table-wrap">
					<table style={{ minWidth: '52rem' }}>
						<thead>
							<tr>
								<td className="alC" colSpan={2}>
									()
								</td>
								<th>Cloud</th>
								<th>Sky</th>
								<th>Mountain</th>
								<th>Vally</th>
							</tr>
						</thead>
						<tbody className="alC">
							<tr>
								<th className="alC" rowSpan={2}>
									
								</th>
								<th className="alC"></th>
								<td>65,000</td>
								<td>95,000</td>
								<td>85,000</td>
								<td>125,000</td>
							</tr>
							<tr>
								<th className="alC"></th>
								<td>85,000</td>
								<td>135,000</td>
								<td>125,000</td>
								<td>165,000</td>
							</tr>
							<tr>
								<th className="alC" rowSpan={2}>
									
								</th>
								<th className="alC"></th>
								<td>80,000</td>
								<td>120,000</td>
								<td>120,000</td>
								<td>150,000</td>
							</tr>
							<tr>
								<th className="alC"></th>
								<td>100,000</td>
								<td>150,000</td>
								<td>150,000</td>
								<td>180,000</td>
							</tr>
							<tr>
								<th className="alC" rowSpan={2}>
									
								</th>
								<th className="alC"></th>
								<td>150,000</td>
								<td>200,000</td>
								<td>200,000</td>
								<td>250,000</td>
							</tr>
							<tr>
								<th className="alC"></th>
								<td>150,000</td>
								<td>200,000</td>
								<td>200,000</td>
								<td>250,000</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>

			<div className="penalty">
				<h2 className="title"> </h2>
				<div className="table-wrap">
					<table style={{ minWidth: '87.1rem' }}>
						<thead>
							<tr>
								<td className="alC">(%)</td>
								<th></th>
								<th>1</th>
								<th>2</th>
								<th>3</th>
								<th>4</th>
								<th>5</th>
								<th>6</th>
								<th>7</th>
								<th>8</th>
								<th></th>
							</tr>
						</thead>
						<tbody className="alC">
							<tr>
								<th> </th>
								<td>100</td>
								<td>90</td>
								<td>80</td>
								<td>70</td>
								<td>60</td>
								<td>50</td>
								<td>40</td>
								<td>30</td>
								<td>20</td>
								<td>10</td>
							</tr>
							<tr>
								<th></th>
								<td>0</td>
								<td>10</td>
								<td>20</td>
								<td>30</td>
								<td>40</td>
								<td>50</td>
								<td>60</td>
								<td>70</td>
								<td>80</td>
								<td>90</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>

			{ddnayo ? (
				<div className="shortcut">
					<a href="https://booking.ddnayo.com/booking-calendar-status?accommodationId=14452" className="designed-border" target="_blank" rel="noreferrer" title="">
						<i className="border-top"></i>
						<h3 className="border-inner"> </h3>
						<i className="border-bottom"></i>
					</a>
				</div>
			) : (
				<>
					<div className="book-date">
						<h2 className="title"> </h2>
						<DateRange {...dateRangeOpt} />
					</div>
					<div className="book-room">
						<ul className="room-select">
							{roomData.map((room) => (
								<Fragment key={room.id}>
									{room.countRoom.map((each, i) => (
										<li key={i} className="checkbox">
											<input
												id={`${each}`}
												type="checkbox"
												value={room.peakPrice}
												onChange={(e) => {
													onCheckedItem(e.target.checked, e.target.id, e.target.value);
												}}
											/>
											<label htmlFor={`${each}`}>
												<p className="h4">{each}</p>
												<span className="price">{room.peakPrice.toLocaleString('ko-KR')}</span>
												<button className="btn-detail" onClick={roomOpen} data-id={room.id}>
													<span className="hide"></span>
												</button>
											</label>
										</li>
									))}
								</Fragment>
							))}
						</ul>
					</div>
					<div className="book-info">
						<table>
							<colgroup>
								<col width="30%" />
							</colgroup>
							<tbody>
								<tr>
									<th></th>
									<td>{returnValue.range}</td>
								</tr>
								<tr>
									<th></th>
									<td>
										{returnValue.night} {returnValue.night + 1}
									</td>
								</tr>
								<tr>
									<th> </th>
									<td>{returnValue.rooms === '' ? '  .' : returnValue.rooms}</td>
								</tr>
								<tr>
									<th></th>
									<td>{(parseInt(returnValue.price) * parseInt(returnValue.night)).toLocaleString('ko-KR')}</td>
								</tr>
							</tbody>
						</table>
					</div>
					<div className="designed-border btn-book">
						<i className="border-top"></i>
						<i className="border-bottom"></i>
						<Link to="/payment" state={{ bookData: returnValue }} className="border-inner">
							<span className="text">&middot;</span>
						</Link>
					</div>
				</>
			)}
			{/* <iframe src="https://booking.ddnayo.com/booking-calendar-status?accommodationId=14452" height="3221px"></iframe> */}
		</div>
	);
}

export default Reservation;
